import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Col, Row, Image, Carousel, Card } from "antd";

export default function CardCarsoulSlick({ content = [],slidesToShow = 3 }) {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow,
    slidesToScroll: 1,
    arrows: true,
    // centerMode: true,
    autoPlay: true,
    nextArrow: <span />,
    prevArrow: <span />
  };

  // const style = {
  //   color: "#fff",
  //   lineHeight: "200px",
  //   // paddingTop: '33px'
  // };
  return (
    <Slider {...settings} className="cardCarasoul">
      {content.map((item, key) => (
        <div>
          <Image
            src={item.img}
            width={150}
            style={{
              objectFit: "contain",
              height: "150px",
            }}
          />
        </div>
      ))}
    </Slider>
  );
}
